"use client"

import { useEffect, useState } from "react"
import Link from "next/link"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { useContract } from "@/context/contract-context"
import { formatDate, formatAddress, filterCampaigns } from "@/lib/utils"
import { SkeletonCard } from "@/components/ui/skeleton-card"

export function ActiveCampaigns({ searchTerm = "" }) {
  const { contract, isConnected } = useContract()
  const [campaigns, setCampaigns] = useState([])
  const [filteredCampaigns, setFilteredCampaigns] = useState([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    if (contract) {
      fetchCampaigns()
    }
  }, [contract])

  // Filter campaigns when searchTerm changes
  useEffect(() => {
    if (searchTerm.trim() === "") {
      setFilteredCampaigns(campaigns)
    } else {
      const filtered = filterCampaigns(campaigns, searchTerm)
      setFilteredCampaigns(filtered)
    }
  }, [searchTerm, campaigns])

  const fetchCampaigns = async () => {
    try {
      setLoading(true)

      // Get campaign count from contract
      const count = await contract.campaignCount()
      const campaignsList = []

      // Fetch each campaign's data
      for (let i = 1; i <= count; i++) {
        try {
          // Get basic campaign info
          const [votingType, restriction, resultType, creator, description] = await contract.getCampaignBasicInfo(i)

          // Get campaign time info
          const [status, startTime, endTime] = await contract.getCampaignTimeInfo(i)

          // Format campaign data
          const campaignData = {
            id: i,
            description: description,
            creator: creator,
            startTime: Number(startTime),
            endTime: Number(endTime),
            status: Number(status),
            restriction: Number(restriction),
            resultType: Number(resultType),
            votingType: Number(votingType),
          }

          campaignsList.push(campaignData)
        } catch (err) {
          console.error(`Error fetching campaign ${i}:`, err)
        }
      }

      setCampaigns(campaignsList)
      setFilteredCampaigns(campaignsList)
    } catch (error) {
      console.error("Error fetching campaigns:", error)
    } finally {
      setLoading(false)
    }
  }

  const getStatusBadge = (startTime, endTime) => {
    const now = Math.floor(Date.now() / 1000)
    let status = "Created"
    let variant = "outline"

    if (now < startTime) {
      status = "Upcoming"
      variant = "outline"
    } else if (now < endTime) {
      status = "Active"
      variant = "default"
    } else {
      status = "Ended"
      variant = "destructive"
    }

    return (
      <Badge variant={variant} className={variant === "default" ? "bg-purple-700 hover:bg-purple-800" : ""}>
        {status}
      </Badge>
    )
  }

  if (loading) {
    return (
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {[1, 2, 3].map((i) => (
          <SkeletonCard key={i} />
        ))}
      </div>
    )
  }

  if (filteredCampaigns.length === 0) {
    return (
      <div className="text-center py-12 bg-white dark:bg-gray-900 rounded-lg shadow-sm border border-gray-200 dark:border-gray-800">
        <h3 className="text-xl font-medium mb-4 text-gray-900 dark:text-gray-100">
          {searchTerm ? "No campaigns found matching your search" : "No campaigns found"}
        </h3>
        <p className="text-muted-foreground mb-6 text-gray-600 dark:text-gray-400">
          Be the first to create a voting campaign!
        </p>
        <Button
          asChild
          className="bg-purple-700 hover:bg-purple-800 text-white dark:bg-purple-600 dark:hover:bg-purple-700"
        >
          <Link href="/create">Create Campaign</Link>
        </Button>
      </div>
    )
  }

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
      {filteredCampaigns.map((campaign) => (
        <Card
          key={campaign.id}
          className="flex flex-col border-purple-900/20 bg-gradient-to-r from-gray-50 to-purple-50 dark:from-gray-950 dark:to-purple-950/20 shadow-sm hover:shadow-md transition-shadow"
        >
          <CardHeader>
            <div className="flex justify-between items-start">
              <CardTitle className="line-clamp-1 text-gray-900 dark:text-gray-100">{campaign.description}</CardTitle>
              {getStatusBadge(campaign.startTime, campaign.endTime)}
            </div>
            <CardDescription className="text-gray-600 dark:text-gray-400">
              Created by: {formatAddress(campaign.creator)}
            </CardDescription>
          </CardHeader>
          <CardContent className="flex-grow">
            <div className="text-sm text-gray-600 dark:text-gray-400">
              <div>Start: {formatDate(campaign.startTime * 1000)}</div>
              <div>End: {formatDate(campaign.endTime * 1000)}</div>
              <div className="mt-2">
                <Badge
                  variant="outline"
                  className="mr-2 border-purple-200 dark:border-purple-800 text-gray-900 dark:text-gray-100"
                >
                  {campaign.votingType === 0 ? "Candidate-based" : "Proposal-based"}
                </Badge>
                <Badge
                  variant="outline"
                  className="border-purple-200 dark:border-purple-800 text-gray-900 dark:text-gray-100"
                >
                  {campaign.restriction === 0 ? "Open" : campaign.restriction === 1 ? "Limited" : "Registration"}
                </Badge>
              </div>
            </div>
          </CardContent>
          <CardFooter>
            <Button
              asChild
              className="w-full bg-purple-700 hover:bg-purple-800 text-white dark:bg-purple-600 dark:hover:bg-purple-700"
            >
              <Link href={`/campaigns/${campaign.id}`}>View Campaign</Link>
            </Button>
          </CardFooter>
        </Card>
      ))}
    </div>
  )
}
